import React from 'react'
import Login from '../component/Authentication.js/Login';
import{Box, Container } from "@chakra-ui/react";

const LoginPage = () => {
  return (
    <Container>
        <Box
       d="flex"
       justifyContent="center"
       p={3}
       bg="pink"
       w="50%"
       m="40px 0 15px 0"
       borderRadius="lg"
       borderWidth="1px"
       marginRight={900}
      
      >
        <text fontSize="4xl" fontFamily="Work sans" color='black'>MiNT-Room-Chat</text>
      </Box >
    <Login/>
    </Container>
  )
}

export default LoginPage;
